.reservation {
  padding-left: 60px;
  padding-right: 60px;
  padding-top: 50px;
  padding-bottom: 50px;
  background-image: url("../..//img/form-bg-desktop@1x.jpg");
  background-size: cover;
  display: flex;
  justify-content: space-between;
  &__promo {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__title {
    margin: 0;
    padding: 0;
    font-size: 40px;
    font-weight: 700;
  }
  &__description {
    margin: 0;
    padding: 0;
    font-size: 18px;
  }
  &__form {
    width: 403px;
    min-height: 455px;
    box-sizing: border-box;
    padding: 50px;
    background-color: #011c40;
    color: #f9fbfd;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    input {
      border: none;
      box-sizing: border-box;
      padding: 16px;
      font-size: 14px;
      color: #011c40;
      background-color: #f9fbfd;
      margin-bottom: 20px;
    }
    label {
      font-size: 10px;
    }
    .checkbox {
      margin-bottom: 30px;
    }
  }
  &__text {
    margin: 0;
    margin-bottom: 30px;
    padding: 0;
    font-size: 18px;
    font-weight: 500;
  }
  .checkbox {
    cursor: pointer;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 23px;
    position: relative;

    & input:checked ~ .checkbox__text::after {
      content: "";
      position: absolute;
      width: 18px;
      height: 18px;
      left: 0;
      top: 2.5px;
      background-image: url("../img/icon-check.svg");
      background-repeat: no-repeat;
      background-position: center;
    }
    &__text {
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 2.5px;
        width: 18px;
        height: 18px;
        background-color: #f9fbfd;
      }
    }
  }
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .reservation {
    background-image: url("../../img/form-bg-desktop@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .reservation {
    padding-left: 45px;
    padding-right: 45px;
    padding-top: 100px;
    padding-bottom: 100px;
    flex-direction: column;
    background-image: url("../../img/form-bg-tablet@1x.jpg");
    &__promo {
      text-align: center;
    }
    &__title {
      margin: 0;
      padding: 0;
      margin-bottom: 25px;
    }
    &__description {
      margin-bottom: 60px;
    }
    &__form {
      width: auto;
    }
  }
}
@media (max-width: 1024px) and (min-resolution: 144dpi),
  (max-width: 1024px) and (min-resolution: 1.5dppx) {
  .reservation {
    background-image: url("../../img/form-bg-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .reservation {
    padding: 65px 15px;
    background-image: url("../../img/form-bg-mobile@1x.jpg");
    &__title {
      font-size: 24px;
      margin-bottom: 15px;
    }
    &__description {
      font-size: 16px;
      margin-bottom: 35px;
    }
    &__form {
      width: auto;
      min-width: 290px;
      min-height: 460px;
      padding-right: 30px;
      padding-left: 30px;
      padding-bottom: 40px;
      padding-top: 35px;
    }
  }
}
@media (max-width: 768px) and (min-resolution: 144dpi),
  (max-width: 768px) and (min-resolution: 1.5dppx) {
  .reservation {
    background-image: url("../../img/form-bg-mobile@2x.jpg");
  }
}
@media (min-width: 1367px) {
  .reservation {
    padding: 0 calc(50vw - 580px);
    padding-bottom: 50px;
    padding-top: 50px;
  }
}
